<template>
    <div class="myMenu">
      <van-nav-bar :border="false">
        <template #left>
                <span class="iconfont icon-arrow-fine-up-copy" @click="goBack">歌单广场</span>
        </template>
        </van-nav-bar>

        <van-tabs v-model="active" sticky>
            <van-tab v-for="(item,index) in titList" :title="item" :key="index">
                    <div class="corcen">
                         <div v-for="(val,i) in menuList" :key="i" class="listItem" @click="goPaths('Playlist',val.id)">
                             <div class="itemImg">
                                 <van-skeleton :round="true" avatar-shape="square" avatar avatar-size=110px :row="3" :loading="loading">
                                    <img v-lazy="val.coverImgUrl">
                                 </van-skeleton>
                                    <p class="Itembos">
                                        <span class="iconfont icon-sanjiaoxing"></span>
                                        <span class="">{{val.playCount | ff_wang}}</span>
                                    </p>
                             </div>
                             <van-skeleton title row-width=100%  :row="1" :loading="loading">
                                <p class="itemText">{{val.name}}</p>
                             </van-skeleton>
                         </div>
                    </div>
            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
    import '../assets/less/Menu.less'
    export default {
        name : 'Menu',
        data() {
            return {
                menuList : [],
                titList  : ['全部','华语','欧美','古风','流行'],
                active   : 0,
                cat      : null,
                loading  : true,
            }
        },
        created() {
           // 获取一类歌单
           this.ff_songList();
          
        },
        methods:{
            goBack(){    
               this.$router.go(-1);
            },
            //获取一类歌单
            ff_songList(){
                  this.loading = true;
                  this.$toast.loading({
                        message: '加载中...',
                        // 禁止点击背景
                        forbidClick: true,
                        loadingType: 'spinner',
                    });
                 this.cat = this.titList.filter((item,index)=>{
                     return (this.active == index)
                 });
                 console.log(this.cat[0])
                 this.axios({
                        url : '/top/playlist',
                        params : {
                            limit : 30,
                            cat   : this.cat[0]
                        }
                    })
                    .then(res=>{
                        if(res.data.code == 200){
                              this.menuList = [];
                              res.data.playlists.map(item=>{
                                    this.menuList.push(item)
                              });
                              this.$toast.clear()
                               setTimeout(()=>{
                                    this.loading = false;
                                },500)
                              
                              console.log(this.menuList)
                        }
                    })
                    .catch(err=>{
                        console.log(err)
                    })
            },
            goPaths(name,id){
               this.$router.push({name,query:{id}});
            },

        },
        watch:{
            active(news,olds){
                this.ff_songList();
            }
        },
        filters:{
            // 化万处理
            ff_wang(news,olds){
                 let newStr = null;
                 newStr = news / 10000;
                 
                 if(newStr >= 1){
                     newStr = `${Math.floor(newStr)}万`;
                     return newStr;
                 }else{
                     return news;
                 }
            }
        }
    }
</script>

<style lang="less" scoped>
       
</style>